@import url('https://fonts.googleapis.com/css2?family=Cinzel:wght@400;600;700&family=Crimson+Text:ital,wght@0,400;0,600;1,400&display=swap');

/* Fuente Ringbearer local */
@font-face {
  font-family: 'Ringbearer';
  src: url('./RingbearerMedium-51mgZ.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

/* ================= Lienzo base (fondo fijo) ================== */
.Invitation{
  min-height:100vh;
  background:
    radial-gradient(1100px 550px at 50% -180px, rgba(212,175,55,.08) 0%, rgba(212,175,55,.04) 35%, rgba(0,0,0,0) 60%),
    radial-gradient(ellipse at center, #0b0f14 0%, #0a0d13 45%, #05070b 100%);
  background-attachment:fixed; /* fondo estático */
  display:flex;
  align-items:flex-start;
  justify-content:center;
  font-family:'Ringbearer','Crimson Text',serif;
  color:#d4af37;
  position:relative;
  overflow-x:hidden;
  padding-top:clamp(120px,18vw,240px); /* hueco para la corona */
}

/* ============== Escena fija (no se mueve al scroll) ============== */
.background-scene{
  position:fixed;
  inset:0;
  z-index:1;
  pointer-events:none;
}

/* Contenedor de la corona (guía del arco) */
.crown{
  position:fixed;
  top:0;
  left:50%;
  transform:translateX(-50%);
  width:min(1100px,92vw);
  height:clamp(240px,28vw,360px); /* altura explícita para posicionar con % */
  pointer-events:none;
}

/* ----------------- Anillo: centrado y debajo ----------------- */
.crown__ring{
  position:absolute;
  left:50%;
  top:100%;
  transform:translate(-50%,-50%);
  width:clamp(320px,44vw,760px);
  height:auto;
  z-index:1; /* debajo de las letras */
  opacity:.96;
  filter:
    drop-shadow(0 6px 22px rgba(0,0,0,.55))
    drop-shadow(0 0 12px rgba(212,175,55,.28))
    brightness(1.06) saturate(1.05) sepia(.18);
}

/* ---------- Letras élficas: arco HACIA ABAJO centrado ---------- */
/* Nota: usamos top + translateY y rotaciones simétricas para
   dibujar una “sonrisa”. scaleX estira hacia fuera.
*/
.crown__elf{
  position:absolute;
  top:8%;                 /* arranca cerca del borde superior de la corona */
  width:clamp(260px,30vw,500px);
  height:auto;
  z-index:2;              /* por encima del anillo */
  opacity:.98;
  /* borde dorado sutil */
  filter:
    drop-shadow(0 0 0.7px rgba(212,175,55,.95))
    drop-shadow(0 0 2px rgba(212,175,55,.42))
    drop-shadow(0 2px 6px rgba(0,0,0,.55))
    brightness(1.08) sepia(.28) hue-rotate(34deg);
  image-rendering:auto;
  will-change:transform,filter;
}

/* Izquierda: desplazada hacia fuera y rotada para seguir el arco */
.crown__elf--left{
  left:20%;
  transform-origin:right center;
  /* centrada, bajada y curvada hacia abajo */
  transform:translate(-70%, 32%) rotate(22deg) scaleX(1) scaleY(1);
}

/* Derecha: simétrica de la izquierda */
.crown__elf--right{
  right:20%;
  transform-origin:left center;
  transform:translate(70%, 32%) rotate(-22deg) scaleX(1) scaleY(1);
}

/* =================== Contenedor del contenido =================== */
.invitation-container{
  position:relative;
  z-index:10;
  width:min(900px,92vw);
  background:rgba(0,0,0,.62);
  border:3px solid rgba(212,175,55,.40);
  border-radius:20px;
  padding:clamp(22px,5vw,50px);
  box-shadow:0 15px 40px rgba(0,0,0,.9);
  backdrop-filter:blur(1px);
  margin:0 auto clamp(24px,4vw,36px);
}

.invitation-content{text-align:center;}

.invitation-title{
  font-family:'Ringbearer','Cinzel',serif;
  font-size:clamp(26px,4vw,42px);
  color:#d4af37;
  margin-bottom:clamp(18px,3.2vw,40px);
  text-shadow:3px 3px 6px rgba(0,0,0,.9);
  letter-spacing:4px;
  text-transform:uppercase;
}

.invitation-text{
  font-family:'Crimson Text',serif;
  font-size:clamp(15px,2.1vw,20px);
  line-height:1.95;
  color:#d4af37;
  text-align:justify;
  text-shadow:2px 2px 4px rgba(0,0,0,.8);
}
.invitation-text p{margin-bottom:22px;text-indent:25px;}
.invitation-text strong{
  color:#ffd700;
  font-weight:600;
  text-shadow:0 0 15px rgba(255,215,0,.6);
  font-size:clamp(16px,2.2vw,22px);
}

.gollum-wrap{
  display: flex;               /* ← coloca imagen y texto en fila */
  align-items: center;
  gap: clamp(10px, 2vw, 24px);
}

/* mejor que scale(0.5): tamaño controlado y responsive */
.gollum-img{
  width: clamp(120px, 18vw, 220px);
  height: auto;
  flex: 0 0 auto;
  display: block;
}

.gollum-text{
  margin: 0;
  text-align: left;            /* el bloque va a la derecha de la imagen */
  line-height: 1.6;
  flex: 1 1 auto;  
            /* ocupa el resto del ancho a la derecha */
}

.gandalf-wrap{
  display: flex;               /* ← coloca imagen y texto en fila */
  align-items: center;
  gap: clamp(10px, 2vw, 24px);
}

.gandalf-img{
  width: clamp(120px, 18vw, 220px);
  transform: scale(1.5);
  height: auto;
  flex: 0 0 auto;
  display: block;
}

.gandalf-text{
  color: crimson;
  font-size: 100%;
  margin: 0;
  text-align: left;            /* el bloque va a la derecha de la imagen */
  line-height: 1.6;
  flex: 1 1 auto;              /* ocupa el resto del ancho a la derecha */
  transition: color .15s ease, background-color .15s ease, box-shadow .15s ease;
  -webkit-tap-highlight-color: rgba(10, 93, 247, 0.25); /* mejora en móviles */
}

/* ====== Solo el bloque relevante a la imagen “cys” + pequeños ajustes de layout ====== */

/* Contenedor de la imagen: centrado sobre el título */
.cys-wrap{
  display: flex;
  justify-content: center;               /* centra horizontalmente */
  align-items: center;
  width: 100%;
  margin: 0 auto clamp(16px, 3vw, 28px); /* espacio inferior antes del título */
  position: relative;                    /* para aplicar viñeta con ::after */
  max-width: min(900px, 92vw);           /* no más ancho que el contenido */
}

/* Imagen con “filtro épico” dorado + viñeta suave */
.cys{
  width: clamp(180px, 38vw, 440px);      /* tamaño responsivo agradable */
  height: auto;
  display: block;
  border-radius: 14px;
  /* Filtro: un punto de sepia y dorado, más contraste y brillo */
  filter:
    contrast(1.08)
    saturate(1.1)
    sepia(.12)
    hue-rotate(18deg)
    brightness(1.05)
    drop-shadow(0 10px 24px rgba(0,0,0,.6))
    drop-shadow(0 0 22px rgba(212,175,55,.15));
  transition: filter .25s ease, transform .25s ease;
}

/* Viñeta dorada muy sutil sobre la imagen (sin tapar) */
.cys-wrap::after{
  content:"";
  position:absolute;
  inset:0;
  border-radius: 14px;
  pointer-events:none;
  background:
    radial-gradient(120% 100% at 50% 60%, rgba(0,0,0,0) 58%, rgba(0,0,0,.35) 100%),
    linear-gradient(to bottom, rgba(212,175,55,.07), rgba(212,175,55,0) 50%);
}

/* Micro-interacción opcional al pasar el ratón */
.cys:hover{
  filter:
    contrast(1.12)
    saturate(1.12)
    sepia(.14)
    hue-rotate(20deg)
    brightness(1.06)
    drop-shadow(0 12px 26px rgba(0,0,0,.65))
    drop-shadow(0 0 26px rgba(212,175,55,.18));
  transform: translateY(-1px);
}

/* --- Por si ya tenías estos (no toco su comportamiento) --- */
.cys-wrap{ gap: clamp(10px, 2vw, 24px); }

/* Apilado en móviles si lo prefieres */
@media (max-width: 540px){
  .gollum-wrap{
    text-align: left;
    margin-top: 20%;
  }
  .gandalf-wrap{
    margin-top: 10%;
  }
  .gollum-text{ text-align: left; }
  .gandalf-text{ text-align: left; }
}

/* ============================ Responsive ============================ */
@media (max-width:1024px){
  .Invitation{padding-top:clamp(100px,16vw,160px);}
  .crown{height:clamp(220px,30vw,330px);}
  .crown__ring{width:clamp(280px,48vw,640px);top:85%;}
  .crown__elf{top:12%;width:clamp(230px,34vw,460px);}
  .crown__elf--left {transform:translate(-54%, 45%) rotate(57deg) scaleX(1.05) scaleY(0.8); left: 18%}
  .crown__elf--right{transform:translate(54%, 45%) rotate(-52deg) scaleX(1) scaleY(1.2); right: 20%}
}

@media (max-width:768px){
  .Invitation{padding-top:clamp(90px,22vw,140px);}
  .crown{height:clamp(200px,34vw,300px);}
  .crown__ring{width:clamp(500px,64vw,520px);top:90%;}
  .crown__elf{top:15%;width:clamp(200px,42vw,360px);}
  .crown__elf--left {transform:translate(-54%, 45%) rotate(57deg) scaleX(1.05) scaleY(0.8); left: 18%}
  .crown__elf--right{transform:translate(54%, 45%) rotate(-52deg) scaleX(1) scaleY(1.2); right: 22%}
  .invitation-container{width:95%;margin:14px auto 24px;padding:clamp(18px,4.5vw,30px);}
}

@media (max-width:480px){
  .Invitation{padding-top:clamp(80px,24vw,120px);}
  .crown{height:clamp(180px,38vw,260px);}
  .crown__ring{top:120%; transform: scale(1.2) rotate(250deg); left: -20%}
  .crown__elf{top:45%;width:clamp(180px,50vw,300px);}
  .crown__elf--left {transform:translate(-54%, 45%) rotate(57deg) scaleX(1.05) scaleY(0.8); left: 18%}
  .crown__elf--right{transform:translate(54%, 45%) rotate(-52deg) scaleX(1) scaleY(1.2); right: 22%}
  .invitation-container{width:98%;padding:clamp(16px,4vw,24px);}
}
