@import url('https://fonts.googleapis.com/css2?family=Cinzel:wght@400;600;700&family=Crimson+Text:ital,wght@0,400;0,600;1,400&display=swap');

/* Importar fuente Ringbearer */
@font-face {
  font-family: 'Ringbearer';
  src: url('./RingbearerMedium-51mgZ.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

.App {
  text-align: center;
  min-height: 100vh;
  background: radial-gradient(ellipse at center, #1a1a2e 0%, #16213e 30%, #0f0f23 70%, #000000 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Ringbearer', 'Crimson Text', serif;
  padding: 20px;
  position: relative;
  color: #d4af37;
  box-sizing: border-box;
}

/* Fondo con patrón de estrellas sutil */
.App::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    radial-gradient(2px 2px at 20px 30px, rgba(255, 255, 255, 0.3), transparent),
    radial-gradient(2px 2px at 40px 70px, rgba(255, 255, 255, 0.2), transparent),
    radial-gradient(1px 1px at 90px 40px, rgba(255, 255, 255, 0.4), transparent),
    radial-gradient(1px 1px at 130px 80px, rgba(255, 255, 255, 0.3), transparent),
    radial-gradient(2px 2px at 160px 30px, rgba(255, 255, 255, 0.2), transparent);
  background-repeat: repeat;
  background-size: 200px 100px;
  animation: twinkle 4s ease-in-out infinite;
  pointer-events: none;
}

@keyframes twinkle {
  0%, 100% { opacity: 0.8; }
  50% { opacity: 1; }
}

.container {
  max-width: 100%;
  width: 100%;
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 20px;
  box-sizing: border-box;
}

/* Texto superior con las tres palabras clickables */
.top-text {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 30px;
  flex-wrap: wrap;
  width: 100%;
  max-width: 600px;
}

.word-button {
  background: linear-gradient(145deg, #2d1810, #1a1a1a);
  border: 2px solid #d4af37;
  color: #d4af37;
  padding: 12px 20px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  border-radius: 0;
  transition: all 0.4s ease;
  text-decoration: none;
  font-family: 'Ringbearer', 'Cinzel', serif;
  position: relative;
  overflow: hidden;
  text-transform: uppercase;
  letter-spacing: 2px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
  min-width: 120px;
  flex: 1;
  max-width: 150px;
}

.word-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(212, 175, 55, 0.3), transparent);
  transition: left 0.6s;
}

.word-button:hover::before {
  left: 100%;
}

.word-button:hover {
  background: linear-gradient(145deg, #d4af37, #b8941f);
  color: #1a1a1a;
  transform: translateY(-3px);
  box-shadow: 0 8px 16px rgba(212, 175, 55, 0.4);
  border-color: #b8941f;
}

.word-button.selected {
  background: linear-gradient(145deg, #d4af37, #b8941f);
  color: #1a1a1a;
  box-shadow: 0 8px 16px rgba(212, 175, 55, 0.4);
  animation: ringPulse 0.8s ease-in-out;
}

.word-button.wrong {
  background: linear-gradient(145deg, red, crimson);
  color: #c60707ca;
  box-shadow: 0 8px 16px rgba(212, 175, 55, 0.4);
  animation: ringPulse 0.8s ease-in-out;
}

@keyframes ringPulse {
  0% { transform: scale(1); box-shadow: 0 8px 16px rgba(212, 175, 55, 0.4); }
  50% { transform: scale(1.05); box-shadow: 0 12px 24px rgba(212, 175, 55, 0.6); }
  100% { transform: scale(1); box-shadow: 0 8px 16px rgba(212, 175, 55, 0.4); }
}

/* Texto del acertijo */
.riddle-text {
  font-size: 24px;
  color: #d4af37;
  margin-bottom: 40px;
  font-weight: 600;
  position: relative;
  font-family: 'Ringbearer', 'Cinzel', serif;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
  letter-spacing: 1px;
  text-align: center;
  width: 100%;
  max-width: 600px;
}

.riddle-text .amigo {
  color: #ffd700;
  margin: 0 10px;
  position: relative;
  font-style: italic;
  text-shadow: 0 0 10px rgba(255, 215, 0, 0.5);
}

.riddle-text .amigo::before {
  content: '';
  position: absolute;
  left: -25px;
  top: -15px;
  width: 16px;
  height: 16px;
  background: radial-gradient(circle, #4a90e2 30%, transparent 70%);
  border-radius: 50%;
  animation: flutter 3s ease-in-out infinite;
  filter: drop-shadow(0 0 5px rgba(74, 144, 226, 0.5));
}

/* Contenedor de las puertas */
.doors-container {
  position: relative;
  width: 100%;
  max-width: 600px;
  height: auto;
  aspect-ratio: 250/339;
  perspective: 1200px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.doors {
  position: relative;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  display: flex;
  align-items: center;
  justify-content: center;
}

.door {
  position: absolute;
  width: 50%;
  height: 100%;
  overflow: hidden;
  border-radius: 0;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.8);
  transition: transform 7s ease-in-out;
  background: transparent;
}

.left-door {
  left: 0;
  transform-origin: left center;
}

.right-door {
  right: 0;
  transform-origin: right center;
}

.door img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  filter: brightness(0.8) contrast(1.1);
  opacity: 1;
  display: block;
}

/* Efecto de apertura de las puertas */
.doors.open .left-door {
  transform: rotateY(-80deg);
}

.doors.open .right-door {
  transform: rotateY(80deg);
}

/* Efecto de luz cuando las puertas se abren */
.doors.open::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at center, rgba(255, 255, 255, 0.3) 0%, transparent 70%);
  animation: lightEffect 2s ease-in-out;
  pointer-events: none;
}

@keyframes lightEffect {
  0% { opacity: 0; }
  50% { opacity: 1; }
  100% { opacity: 0; }
}

/* Animaciones */
@keyframes float {
  0%, 100% { transform: translateX(-50%) translateY(0px); }
  50% { transform: translateX(-50%) translateY(-10px); }
}

@keyframes twinkle {
  0%, 100% { opacity: 1; transform: translateX(-50%) scale(1); }
  50% { opacity: 0.7; transform: translateX(-50%) scale(1.1); }
}

@keyframes flutter {
  0%, 100% { transform: translateY(0px) rotate(0deg); }
  25% { transform: translateY(-5px) rotate(5deg); }
  50% { transform: translateY(-10px) rotate(0deg); }
  75% { transform: translateY(-5px) rotate(-5deg); }
}

/* Responsive para tablets */
@media (max-width: 1024px) {
  .container {
    padding: 15px;
  }
  
  .top-text {
    gap: 15px;
  }
  
  .word-button {
    padding: 10px 16px;
    font-size: 14px;
    letter-spacing: 1px;
    min-width: 100px;
  }
  
  .riddle-text {
    font-size: 22px;
    margin-bottom: 30px;
  }
  
  .doors-container {
    max-width: 500px;
  }
}

/* Responsive para móviles */
@media (max-width: 768px) {
  .App {
    padding: 10px;
  }
  
  .container {
    padding: 10px;
    min-height: 100vh;
  }
  
  .top-text {
    gap: 10px;
    margin-bottom: 20px;
    flex-direction: column;
    align-items: center;
  }
  
  .word-button {
    padding: 12px 20px;
    font-size: 16px;
    letter-spacing: 1px;
    min-width: 200px;
    max-width: 250px;
    width: 100%;
  }
  
  .riddle-text {
    font-size: 20px;
    margin-bottom: 25px;
    line-height: 1.4;
  }
  
  .riddle-text .amigo::before {
    left: -20px;
    top: -10px;
    width: 12px;
    height: 12px;
  }
  
  .doors-container {
    max-width: 400px;
    width: 100%;
  }
}

/* Responsive para móviles pequeños */
@media (max-width: 480px) {
  .App {
    padding: 5px;
  }
  
  .container {
    padding: 5px;
  }
  
  .word-button {
    padding: 10px 16px;
    font-size: 20px;
    min-width: 360px;
  }
  
  .riddle-text {
    font-size: 18px;
    margin-bottom: 20px;
  }
  
  .doors-container {
    max-width: 300px;
  }
}
